<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <!--
        <div>Room # {{ this.$route.params.id }}</div>
        <p>votedAll: {{votedAll}}</p>
        <p>this.user: {{user}}</p>
        <p>this.users: {{users}}</p>
        <p>this.forceShowVotes: {{forceShowVotes}}</p>
        <p>this.roundFinished: {{roundFinished}}</p>
        -->
      </v-col>
    </v-row>

    <v-row>
      <v-col cols="8">
        <ul class="cards d-flex justify-center">
          <li class="card-wrapper" v-for="(point) in points" :key="point">
            <div class="card mx-2" v-ripple @click="cardClick(point)" :class="cardClasses(point)">
              <div class="card__icon left py-1 pl-1">{{ point }}</div>
              <div class="card__center">{{ point }}</div>
              <div class="card__icon right py-1 pl-1">{{ point }}</div>
            </div>
          </li>
        </ul>
      </v-col>
      <v-col cols="4">
        <v-card outlined>
          <v-card-title class="grey lighten-4">
            <!--
            <div class="text-overline">PLAYERS</div>
            -->
            <h3 class="primary--text overline">{{ roundFinished && showStatistics ? 'Round results' : 'Players' }}</h3>
              <v-spacer />
              <div class="my-2">
                <v-tooltip bottom v-if="roundFinished">
                  <template v-slot:activator="{ on }">
                    <v-btn
                      :color="toggleResultsHover ? 'green' : ''"
                      icon
                      fab
                      small
                      v-on="on"
                      @click="toggleResults"
                      @mouseenter="toggleResultsHover = true"
                      @mouseleave="toggleResultsHover = false"
                    >
                      <v-icon>mdi-cached</v-icon>
                    </v-btn>
                  </template>
                  <span>Toggle Results</span>
                </v-tooltip>

                <v-tooltip bottom>
                  <template v-slot:activator="{ on }">
                    <v-btn
                      :color="showVotesHover ? 'info' : ''"
                      icon
                      fab
                      small
                      v-on="on"
                      @click="showVotes"
                      @mouseenter="showVotesHover = true"
                      @mouseleave="showVotesHover = false"
                    >
                      <v-icon>mdi-eye-outline</v-icon>
                    </v-btn>
                  </template>
                  <span>Show Votes</span>
                </v-tooltip>

                <v-tooltip bottom>
                  <template v-slot:activator="{ on }">
                    <v-btn
                      :color="clearVotesHover ? 'error' : ''"
                      icon
                      fab
                      small
                      v-on="on"
                      @click="clearVotes"
                      @mouseenter="clearVotesHover = true"
                      @mouseleave="clearVotesHover = false"
                    >
                      <v-icon>mdi-trash-can-outline</v-icon>
                    </v-btn>
                  </template>
                  <span>Clear Votes</span>
                </v-tooltip>
              </div>
          </v-card-title>

          <v-list>
            <div v-if="roundFinished && showStatistics">
              <v-list-item>
                <v-list-item-content>
                  <!--
                  <h3 class="primary--text overline">Round Results</h3>
                  -->
                  <h6 class="my-2 grey--text">
                    Avg: <v-badge inline :content="calcAverage(statistics)" />
                    / Votes: <v-badge inline :content="votesCount" />
                    / Voters: <v-badge inline :content="votersCount" />
                    / Observers: <v-badge inline :content="observesCount" />
                  </h6>
                </v-list-item-content>
              </v-list-item>

              <v-divider inset></v-divider>

              <v-list-item>
                <span class="mr-3 grey--text overline">Point</span>
                <v-list-item-content class="grey--text overline py-0">
                  Votes
                </v-list-item-content>
              </v-list-item>

              <v-list-item v-for="(val, key, ind) in statistics" :key="ind">
                <v-list-item-avatar>
                  <v-btn color="primary" fab small outlined>
                  <h2>{{ key }}</h2>
                  </v-btn>
                </v-list-item-avatar>
                <v-list-item-content>
                  {{ val }} vote{{val > 1 ? 's' : '' }}
                </v-list-item-content>
              </v-list-item>
              <v-divider inset v-if="Object.keys(statistics).length === 1"></v-divider>
              <v-list-item v-if="Object.keys(statistics).length === 1">
                <v-list-item-content>
                  <h3 class="green--text text-center">CONSENSUS!</h3>
                </v-list-item-content>
              </v-list-item>
            </div>

            <div
              v-else
              v-for="(user, index) in [{ name: user.name, observer: user.observer, vote }, ...users]"
              :key="user.id">
              <v-list-item>
                <v-list-item-avatar>
                  <v-btn color="primary" fab small outlined v-if="index === 0">
                    <v-icon v-if="user.observer">mdi-eye</v-icon>
                    <v-icon v-else>mdi-account</v-icon>
                  </v-btn>
                  <v-btn color="grey lighten-2" fab small outlined v-else-if="user.observer">
                    <v-icon>mdi-eye</v-icon>
                  </v-btn>
                  <v-btn color="blue lighten-2" fab small dark v-else>
                    <v-icon>mdi-account</v-icon>
                  </v-btn>
                </v-list-item-avatar>

                <v-list-item-content>
                  <h3
                    v-if="index === 0"
                    class="primary--text">{{ user.name }}</h3>
                  <div
                    v-else
                    :class="user.observer ? 'grey--text lighten-3' : 'black--text lighten-5'">{{ user.name }}</div>
                </v-list-item-content>
                <v-list-item-icon :class="[{ 'text-h5': index === 0, 'text-subtitle-1': index !== 0 }]">
                  <div
                    :class="index === 0 ? 'primary--text' : 'black--text lighten-3'"
                    v-if="index === 0 || roundFinished/* (votedAll && vote) */ || forceShowVotes">
                    {{ user.vote === true ? '' : user.vote }}
                  </div>
                  <v-icon v-if="index !== 0
                    && !roundFinished/* !votedAll */
                    && !forceShowVotes" :color="!user.vote ? 'grey lighten-3' : 'green'">
                    mdi-check-circle-outline
                  </v-icon>
                </v-list-item-icon>
              </v-list-item>

              <v-divider inset v-if="index === 0"></v-divider>
            </div>
          </v-list>

        </v-card>
      </v-col>
    </v-row>
    <v-snackbars
      :style="{opacity: 0.7}"
      :objects.sync="messages"
    />
  </v-container>
</template>

<script>
import io from 'socket.io-client';
import VSnackbars from 'v-snackbars';
import { mapState } from 'vuex';

export default {
  name: 'Room',
  components: {
    'v-snackbars': VSnackbars,
  },
  data() {
    return {
      showStatistics: true,
      toggleResultsHover: false,
      showVotesHover: false,
      clearVotesHover: false,
      forceShowVotes: false,
      roundFinished: false,
      points: ['0', '', '1', '2', '3', '5', '8', '13', '?', ''],
      socket: {},
      vote: null,
      users: [],
      messages: [],
      statistics: {},
    };
  },
  computed: {
    ...mapState({
      user: (state) => state.common.user,
      room: (state) => state.common.room,
    }),
    votedAll() {
      // eslint-disable-next-line
      return [{ name: this.user, vote: this.vote }, ...this.users]
        .filter((usr) => !usr.observer)
        .every((usr) => usr.vote);
    },
    votesCount() {
      return Object.values(this.statistics).reduce((acc, val) => acc + val, 0) || '0';
    },
    votersCount() {
      return [this.user, ...this.users].filter((u) => !u.observer).length || '0';
    },
    observesCount() {
      return [this.user, ...this.users].filter((u) => u.observer).length || '0';
    },
  },
  methods: {
    cardClick(point) {
      // console.log('Card click');
      if (this.user.observer) {
        this.messages.push({
          message: 'Observers can\'t vote',
          bottom: true,
          right: true,
          color: 'red',
          timeout: 7000,
        });
        return;
      }

      if (this.roundFinished) {
        this.messages.push({
          message: 'Vote can\'t be changed once voting is completed',
          bottom: true,
          right: true,
          color: 'red',
          timeout: 7000,
        });
        return;
      }
      this.vote = point;
      this.socket.emit('vote', { roomId: this.room.id, vote: this.vote });
    },
    cardClasses(index) {
      return { active: this.vote === index };
    },
    showVotes() {
      // console.log('Show votes click');
      this.socket.emit('show_votes', { roomId: this.room.id });
    },
    clearVotes() {
      // console.log('Clear votes click');
      this.socket.emit('clear_votes', { roomId: this.room.id });
    },
    toggleResults() {
      this.showStatistics = !this.showStatistics;
    },
    calculateStatistics(votes) {
      return votes.reduce((acc, el) => {
        if (acc[el]) {
          acc[el] += 1;
        } else {
          acc[el] = 1;
        }
        return acc;
      }, {});
    },
    calcAverage(stat) {
      const values = Object.keys(stat);
      if (values.length === 0) return '0.00';
      // console.log('values', values);
      return (values.reduce((acc, v) => {
        let curVal = +v;
        if (v === '') {
          curVal = 0.5;
        } else if (['?', ''].includes(v)) {
          curVal = 0;
        }
        return acc + curVal;
      }, 0) / values.length).toFixed(2);
    },
  },
  created() {
    // this.socket = io('http://localhost:5000');
    this.socket = io();
    // console.log('~~~SocketInstance', this.socket);

    this.socket.emit('join', { room: this.room, user: this.user, date: new Date() });

    this.socket.on('room_join', (user) => {
      // console.log('~~~room_join', user);
      this.users.push(user);
      this.forceShowVotes = false;
      if (!user.observer) {
        this.roundFinished = false;
      }
      this.messages.push({
        message: `${user.name || 'Unnamed user'} joined the room`,
        bottom: true,
        right: true,
        color: 'blue',
        timeout: 7000,
      });
      // this.$set(this.users, user.id, user.name);
    });

    this.socket.on('room_left', (id) => {
      // console.log('~~~room_left', id);
      // console.log('this.users', this.users);
      // console.log('this.users.length', this.users.length);
      const indexToDelete = this.users.findIndex((u) => u.id === id);
      const leftUserName = this.users[indexToDelete].name || 'Unnamed user';
      // console.log('indexToDelete', indexToDelete);
      this.$delete(this.users, indexToDelete);
      // console.log('users refresh', this.users);
      this.messages.push({
        message: `${leftUserName} left the room`,
        bottom: true,
        right: true,
        color: 'orange',
        timeout: 7000,
      });
      // delete this.users[id];
      // this.$delete(this.users, id);
      // Vue.delete(this.users, id);
    });

    this.socket.on('init', (data) => {
      // console.log('~~~init', data);
      this.users.push(...data.room.users);
      this.roundFinished = data.room.roundFinished;
      // this.users = this.users.concat(data.roomUsers);
      this.messages.push({
        message: `Hi ${this.user.name}, welcome to the planning poker session!`,
        bottom: true,
        right: true,
        color: 'green',
        timeout: 7000,
      });
    });

    this.socket.on('vote', (data) => {
      // console.log('~~~vote', data);
      const userIndex = this.users.findIndex((u) => u.id === data.userId);
      // this.users[userIndex].vote = data.vote;
      this.messages.push({
        message: `${this.users[userIndex].name} voted!`,
        bottom: true,
        right: true,
        color: 'green',
        timeout: 7000,
      });

      if (!this.users[userIndex].vote) {
        this.$set(this.users[userIndex], 'vote', data.vote);
      }
    });

    this.socket.on('round_finished', (data) => {
      // console.log('~~~round_finished', data);
      this.roundFinished = true;
      // eslint-disable-next-line
      this.users.forEach((user) => user.vote = data.votes[user.id]);
      this.statistics = data.statistics; // this.calculateStatistics(Object.values(data));
      this.showStatistics = true;
    });

    this.socket.on('show_votes', (data) => {
      console.log('~~~show_votes', data);
      this.forceShowVotes = true;
      this.roundFinished = true;
      const userName = data.openUserId || 'Unnamed user';
      // eslint-disable-next-line
      this.users.forEach((user) => user.vote = data.votes[user.id]);
      this.messages.push({
        message: `${userName}, finished round by opening votes!`,
        bottom: true,
        right: true,
        color: 'orange',
        timeout: 7000,
      });
    });

    this.socket.on('clear_votes', () => {
      // console.log('~~~clear_votes', data);
      // eslint-disable-next-line
      this.users.forEach((user) => user.vote = null);
      this.vote = null;
      this.forceShowVotes = false;
      this.roundFinished = false;
      this.messages.push({
        message: 'New voting round started!',
        bottom: true,
        right: true,
        color: 'blue',
        timeout: 7000,
      });
    });
  },
};
</script>

